.nav__ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  height: 134px;
  margin: 32px 19px 0;
}

.card__description {
  color: #060b35;
  margin-top: 40px;
  margin-inline: 16px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__description:hover {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 16px;
  margin-inline: 16px;
  line-height: 14px;
}

.card__stars-rating {
  margin-inline: 16px;
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  text-align: right;
  justify-content: flex-end;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__buy {
  color: #fff;
  text-align: center;
  cursor: pointer;
  text-transform: uppercase;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  margin: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__buy:hover {
  color: #00acdc;
  background: #fff;
}

.card__price {
  justify-content: space-between;
  margin-inline: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-number {
  color: #060b35;
  text-align: right;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.stars {
  justify-content: center;
  align-items: center;
  display: flex;
}

.stars__image {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__image:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.nav__ul {
  display: flex;
}

.nav__item {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
  position: relative;
}

.nav__item:hover, .nav__item.is-active {
  color: #00acdc;
}

.nav__item.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__items:not(:last-child) {
  margin-right: 20px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header-logo {
  width: 40px;
  height: 40px;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
  font-size: 10px;
}

@media (width <= 487px) {
  .main {
    grid-template-rows: repeat(8, auto);
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) and (width <= 767px) {
  .main {
    grid-template-rows: repeat(4, auto);
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) and (width <= 1023px) {
  .main {
    grid-template-rows: repeat(3, auto);
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-rows: repeat(2, auto);
    grid-template-columns: repeat(4, 200px);
  }
}

.main {
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

* {
  box-sizing: border-box;
}

